body {
  margin: 0;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
a{
  text-decoration: none;
}
.navbar{
  width: 100%;
  height: 47px;
  line-height: 47px;
  background-color: rgb(8,26,48);
  text-align: center;
  word-spacing: 15px;
  position: fixed;
  z-index: 1;
  /* float: left; */
  overflow: hidden;
}
.navitem{
  /* color: rgb(171,188,204); */
  color: rgb(51,153,254);
  font-weight: 400;
}
.main{
  width: 100%;
  background-color: rgb(0,29,59);
  position: relative;
  float: left;

}
.display-picture-container{
  width: 270px;
  height: 270px;
  border-radius: 2%;
  margin: auto;
  outline: 2px solid rgb(51,153,254);
  margin-top: 5%;
  position: relative;
  overflow: hidden;
}
.dp-dark-scarf{
  width: 100%;
  height: inherit;
  position: relative;
  float: left;
  background-color: rgb(0,29,59);
  opacity: 0.5;
}
.bg-content{
  text-align: center;
  margin-top: 2%;
  position: relative;
  margin-bottom: 15%;
}
progress{
  width: 100%;
  height: 45px;
}
.services{
  display: flex;
  justify-content: space-evenly;
}
.services-card{
  width: 100%;
  text-align: center;
  border-radius: 10px;
  padding: 12px;
  cursor: pointer;
}
.services-card:hover{
  outline: 1px solid rgb(51,153,254);
}
.hire-bg{
  width: 100%;
  height: 500px;
  position: relative;
  float: left;
  background-image: url(/public/images/hirebg.gif);
  background-attachment: fixed;
}
.dark-scarf{
  width: 100%;
  height: inherit;
  position: relative;
  float: left;
  background-color: rgb(0,29,59);
  opacity: 0.5;
}
@media only screen and (max-width:1007px){
  .display-picture-container{
    margin-top: 10%;
  }
  .hire-bg{
    height: 400px;
  }
}
@media only screen and (max-width: 640px){
  .display-picture-container{
    margin-top: 20%;
  }
  .services{
    display: inline;
  }
  .hire-bg{
    height: 400px;
  }
}
.footer{
  width: 100%;
  height: auto;
  background-color: rgb(8,19,25);
  text-align: center;
  position: relative;
  float: left;
}
.footer-icons{
  word-spacing: 10px;
}
.footer-icon{
  margin-top: 1px;
  text-align: center;
  font-size: 20px;
  cursor: pointer;
  color: rgb(171,188,204);
}
.img{
  max-width: 100%;
  height: auto;
}
.container{
  width: 92%;
  margin: 4%;
  position: relative;
  float: left;
}